@import './variables.scss';
@font-face {
    font-family: 'ARIAL-Regular';
    font-style: normal;
    font-weight: normal;
    src:url('./fonts/ARIAL.TTF'),
    url('./fonts/ARIAL.eot') format('eot'),
    url('./fonts/ARIAL.otf') format('otf'),
    url('./fonts/ARIAL.woff') format('woff'),
    url('./fonts/ARIAL.woff') format('woff'),
    url('./fonts/ARIAL.svg') format('svg');
    }

body {
    font-family: $fontfamilyAssistant;
    font-weight: $fontweight400;
    font-size: $fontsize14px;
    background: #F5F5F5;
}

a,
button {
    transition: 0.5s all ease-in-out;
}
label {
    color: #716F6F;
}
button.btn.btn-primary.save {
    background: #6DB0E1;
    border-radius: 5px;
    // box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    border-color: #6DB0E1;
    width: $width100per;
    height: 57px;
    font-style: normal;
    font-weight: $fontweight400;
    font-size: 25px;
    line-height: 91.65%;
    color: #FFFFFF;

    &:focus {
        background: #6DB0E1;
    }

    &:hover {
        opacity: 0.8;
    }
}
.fileupload.uploadImage {
    button.crossBtn {
        position: absolute;
        right: 10px;
        top: 10px;
        border: none;
        background: #fff;
        border-radius: 100px;
        height: 24px;
        width: 24px;
        padding: 0;
        z-index: 9;
    }
    .imgUpload {
        padding-top: 0 !important;
    }
}

    img.profilePreviewImg {
        // width: 7rem;
        // height: 3rem;
        margin-bottom: 0.4rem;
    }

.previewImg {
    margin-bottom: -11px;
}
.fileupload {
    background: #E9EEF2;
    border-radius: 5px;
    padding: 10px;
    position: relative;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    height: 175px;

    &::after {
        position: absolute;
        left: 10px;
        right: 10px;
        width: calc(100% - 20px);
        border: 2px dashed #B2C1C0;
        border-radius: 5px;
        height: calc(100% - 20px);
        content: '';
        margin: auto;
    }

    input[type="file"] {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        opacity: 0;
    }

    p {
        font-style: normal;
        font-weight: 600;
        font-size: 10px;
        line-height: 91.65%;
        color: #91A0AB;
        margin-bottom: 5px;
    }

    strong {
        font-style: normal;
        font-weight: 600;
        font-size: 17px;
        line-height: 91.65%;
        color: #91A0AB;
        margin-bottom: 5px;
    }

    button.folder-upload.btn.btn-primary {
        // background: #91A0AB;
        // border: 1px solid #91A0AB;
        border-radius: 3px;
        // width: 115px;
        position: relative;
        z-index: 1;
        font-style: normal;
        font-weight: 600;
        font-size: 10px;
        line-height: 91.65%;
        color: #FFFFFF;
        // margin-bottom: 16px;
        // height: 22px;

        input[type="file"] {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            cursor: pointer;
        }
    }
}
table.table {
    border-collapse: separate;
    border-spacing: 0 2px;
}
.crossBtnwrap {
    button.btn.btn-primary, button.btn.btn-primary:active, button.btn.btn-primary:focus {
        background: transparent;
        border: none;
        padding: 5px;
        box-shadow: none;
        img {
            width: 25px;
        }
    }
}

.wrapper {
    display: $displayInlineBlock;
    width: $width100per;
    position: $positionRelative;

    .top-bar {
        width: $width100per;
        position: $positionRelative;
        z-index: 2;
        background: #fff;
        padding: 15px;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.10);
        border-radius: 10px;
        display: flex;
        justify-content: space-between;
        .logoWrap {
            display: flex;
            align-items: center;
            img {
                width: 60px;
            }
            h2 {
                font-style: normal;
                font-weight: 800;
                font-size: 20px;
                line-height: 91.65%;
                color: #6DB0E1;
                font-family: $fontfamilyAssistant;
                margin-left: 15px;
                margin-bottom: 0;
            }
            }

            .dropdown {
                button#dropdown-item-button {
                    width: 45px;
                    height: 45px;
                    background-color: #6DB0E1;
                    border-radius: 100px;
                    background-image: url('../images/userIcon.svg');
                    background-position: center center;
                    background-size: auto;
                    background-repeat: no-repeat;
                    border: 1px solid #6DB0E1;
                    &::after {
                        display: none;
                    }
                    &:focus {
                        box-shadow: none;
                    }
                }
               .dropdown-menu {
                    p {
                        padding: 0.25rem 1.5rem;
                        margin-bottom: 0;
                        span {
                            display: block;
                            font-size: 12px;
                            color: #9F9F9F;
                        }
                    }
                }
            }

        
    }

    // Sidebar CSS Start

    .main-sidebar {
        width: 250px;
        display: $displayInlineBlock;
        padding-left: 5px;
        padding-right: 5px;
        height: calc(100vh - 0px);
        position: $positionFixed;
        left: 0;
        top: 80px;
        background: #F5F5F5;
        z-index: 5;
        padding-top: 25px;
        -webkit-transition: left 1s ease-in-out;
        -moz-transition: left 1s ease-in-out;
        -o-transition: left 1s ease-in-out;
        transition: left 1s ease-in-out;

        .logo-wrap {
            padding-left: 50px;

            a {
                padding-left: 0;

                &:focus {
                    outline: none;
                }
            }
            a {
                img {
                    width: 115px;
                }
            }
        }

        .flex-column.nav {
            margin-top: 54px;

            a.manage.nav-link {
                background-image: url('../images/manageIcon.svg');
                background-position: 10px center;
                background-repeat: no-repeat;
                background-size: 21px 21px;
            } 
            a.message.nav-link {
                background-image: url('../images/directinbox.svg');
                background-position: 10px center;
                background-repeat: no-repeat;
                background-size: 21px 21px;
            }

            a.review.nav-link {
                background-image: url('../images/reviewIcon.svg');
                background-position: 10px center;
                background-repeat: no-repeat;
                background-size: 21px 21px;
            }
            a.statistic.contact.nav-link, a.statistic.contact.nav-link.active {
                background-image: none;
            }
            a.review.nav-link.active {
                background-image: url('../images/reviewIconWhite.svg');
            }

            a.statistic.nav-link {
                background-image: url('../images/statisticIcon.svg');
                background-position: 10px center;
                background-repeat: no-repeat;
                background-size: 21px 21px;
            }

            a.statistic.nav-link.active {
                background-image: url('../images/statisticIconWhite.svg');
            }

            a.nav-link {
                font-weight: 400;
                font-size: 18px;
                line-height: 91.65%;
                color: #3C3C3C;
                padding: 16px 50px;
                font-family: $fontfamilyArialRegular;
                text-transform: capitalize;

                &:focus {
                    outline: none;
                }
            }

            a.nav-link.active {
                // background-image: url('../images/tickWhite.svg');
                background-position: 10px center;
                background-repeat: no-repeat;
                background-size: 21px 21px;
                background-color: #E1E1E1;
                border-radius: 5px;
                color: #6DB0E1;
                font-weight: 700;
            }


            ul {
                padding-left: 0;
                margin-bottom: 0;

                a.nav-link {
                    font-style: normal;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 91.65%;
                    color: #9F9F9F;
                    font-family: $fontfamilyArialRegular;
                }
            }
            h2.accordion-header {
                margin-bottom: 0;
                button.accordion-button {
                    border: none;
                    width: 100%;
                    text-align: left;
                    background: transparent;
                    padding-left: 0;
                    padding-right: 0;
                    a.nav-link {
                        font-weight: 400;
                        font-size: 18px;
                        line-height: 91.65%;
                        color: #3C3C3C;
                        padding: 16px 50px;
                        font-family: "ARIAL-Regular";
                        text-transform: capitalize;
                        img {
                            transition: 0.5s all ease-in-out;
                        }
                    }
                    a.nav-link.active {
                        background-color: transparent;
                    }
                }
                button.accordion-button.collapsed {
                    a.nav-link.active, a.nav-link {
                        font-weight: 400;
                        font-size: 18px;
                        line-height: 91.65%;
                        color: #3C3C3C;
                        padding: 16px 50px;
                        font-family: "ARIAL-Regular";
                        text-transform: capitalize;
                        background-color: transparent;
                    }
                }
                button.accordion-button[aria-expanded="true"] {
                    a.manage.nav-link {
                        img {
                            transform: rotate(180deg);
                            transition: 0.5s all ease-in-out;
                        }
                    }
                }
            }
            
        }
    }

